  p1: 'طبقا للماده (175) من اللائحة التنفيذية ينقضي الصندوق إذا انتهت مدته ولم يتم تجديده أو إذا تحقق الغرض الذي أسس الصندوق من أجله أو واجهته ظروف تحول دون مزاولته لنشاطه.D697',
